var token = getFromLocalStorage("token");
var id_perfil = getFromLocalStorage("id_perfil");
var idFilmSerie = getFromLocalStorage("id");
var tipo = getFromLocalStorage("tipo");
var filmSerie = {};
var filmSerieTemps = {};
var tempSelected = "0-temporada";
var filmSerieEpisodes = {};
var mainListAll = {};
var dataListAll = {};
var cursor = 0;
var modelCursor = 0;
var cursorEpisode = 0;
var imageFilmSerie = "";
var type_item = "";
var onModel = false;
var onEpisodes = false;
var listOn = false;
var onModal = false;
var swiper;
var positionKeep = 0;
var containerElement = document.getElementById("container");
var loadingContainerElement = document.getElementById("loading-container");
var detailsRating = document.getElementById("details--rating");

var keepWatchingContainer = document.getElementById("keep-watching-container");

let shouldShowKeepWatching = false;
let isKeepWatchingContainerOpen = false;
let horizontalCursor = 0;
let continueButton = document.getElementById("continue-button");
let restartButton = document.getElementById("restart-button");
var continueMovie;

let oldx = 0;
let oldy = 0;
let mouseTimeout;
let webOsSys = getFromLocalStorage("system");
if (!token) goTo("../../index.html");

async function main() {
  createWindowVariables();

  containerElement.style.display = "none";
  loadingContainerElement.style.display = "flex";
  let data = await postDetails(
    "https://watch.tv.br/v3/title?id_perfil=" +
    id_perfil +
    "&id=" +
    idFilmSerie +
    "&tipo=" +
    tipo +
    "&token=" +
    token
  );
  let dataList = await getDataList(
    "https://watch.tv.br/v2/profile/data?id_perfil=" +
    id_perfil +
    "&token=" +
    token
  );

  const dataItemContinue = dataList.keep_watching.find((dataItem) => {
    return dataItem.id == idFilmSerie;
  });

  filmSerie = data;

  if (filmSerie.warning) {
    detailsRating.innerHTML += filmSerie.warning;
  } else {
    detailsRating.innerHTML = "";
  }

  if (tipo === "serie") {
    if (dataItemContinue) {
      data.seasons.map((season, index) => {
        if (season.order == dataItemContinue.season_order) {
          filmSerieTemps = filmSerie.seasons[index];
          filmSerieEpisodes = filmSerie.seasons[index].videos;
        }
      });
    } else {
      filmSerieTemps = filmSerie.seasons[0];
      filmSerieEpisodes = filmSerie.seasons[0].videos;
    }
  }

  let mainList = await getMainList(
    "https://watch.tv.br/v3/main?id_perfil=" + id_perfil + "&token=" + token
  );
  mainListAll = mainList;

  dataListAll = dataList;

  imageFilmSerie = buildUrlImage(filmSerie.cdn, filmSerie.cover);

  if (tipo === "serie") type_item = "show";
  else type_item = "movie";

  document.getElementById("details--button").classList.add("selected");

  buildingHTMLDetails(filmSerie, imageFilmSerie);
  if (tipo === "serie") {
    buildingHTMLEpisodes(filmSerieEpisodes);
    buildingHTMLButtonTemp(filmSerieTemps);
  } else document.getElementById("details-temp-button").style.display = "none";

  containerElement.style.display = "block";
  loadingContainerElement.style.display = "none";

  dataListAll.my_list.map((item) => {
    if (item.id == idFilmSerie) {
      replaceMyList();
    }
  });

  setInterval(checkUser, 5000);

  arrowDown();

  const keepMovie = dataListAll.keep_watching.find((element) => {
    return element.id == idFilmSerie;
  });

  if (tipo == "filme" && keepMovie.position > 0) {
    const button = document.getElementById("details-button-episode");
    button.innerText = "Continuar";
    continueMovie = keepMovie;
    shouldShowKeepWatching = true;
  } else {
    const button = document.getElementById("details-button-episode");

    continueMovie = keepMovie;
    shouldShowKeepWatching = false;
  }

  swiper = new Swiper(".details--temps-list", {
    slidesPerView: "auto",
    slidesOffsetAfter: 1000,
    loop: false,
    observer: true,
    observeParents: true,
  });
}

function goBack() {
  history.back();
}

window.onload = function () {
  document.addEventListener("keydown", function (e) {
    e.preventDefault();
    switch (e.keyCode) {
      case 8:
        e.preventDefault();
        goBack();
        break;
      case 461:
        window.history.back();
        break;
      case 37:
        e.preventDefault();
        arrowLeft();
        break;
      case 38:
        e.preventDefault();
        arrowUp();
        break;
      case 39:
        e.preventDefault();
        arrowRight();
        break;
      case 40:
        e.preventDefault();
        arrowDown();
        break;
      case 13:
        e.preventDefault();
        enterAction();
        break;
      default:
        break;
    }
  });

  window.addEventListener("auxclick", function (e) {
    if (e.button == 1) {
      enterAction();
    }
  });

  window.addEventListener("wheel", function (event) {
    if (event.deltaY < 0) {
      arrowUp();
    } else if (event.deltaY > 0) {
      arrowDown();
    }
  });
};

function arrowRight() {
  if (isKeepWatchingContainerOpen) {
    if (horizontalCursor == 0) {
      horizontalCursor = 1;
      continueButton.classList.remove("selected-button");
      restartButton.classList.add("selected-button");
      return;
    }
  } else {
    if (cursor === 4 && cursorEpisode < filmSerieEpisodes.length - 1) {
      setCursorEpisode(true, 1);
      settingSelectedEpisode(-1);
      navegationOnPage();
      settingSelected(1);
      swiperSlide(true);
    } else {
      if (cursor === 1) {
        setCursor(cursor + 1 <= 4, 1);
        navegationOnPage();
      }
    }
  }
}

function arrowLeft() {
  if (isKeepWatchingContainerOpen) {
    if (horizontalCursor == 1) {
      horizontalCursor = 0;
      continueButton.classList.add("selected-button");
      restartButton.classList.remove("selected-button");
      return;
    }
  } else {
    if (cursor === 4) {
      setCursorEpisode(cursorEpisode - 1 >= 0, -1);
      settingSelectedEpisode(1);
      navegationOnPage();
      settingSelected(-1);
      swiperSlide(false);
    } else {
      if (cursor === 2) {
        setCursor(cursor - 1 >= 0, -1);
        navegationOnPage();
      }
    }
  }
}

function arrowUp() {
  if (isKeepWatchingContainerOpen) return;
  if (onModel) setModelCursor(modelCursor - 1 >= 0, -1);
  setCursor(cursor - 1 >= 0, -1);

  if (cursor == 2 || cursor == 1) {
    setCursor(cursor - 1 >= 0, -1);
    navegationOnPage();
    settingSelected(1);
  } else {
    navegationOnPage();
    settingSelected(1);
  }
}

function arrowDown() {
  if ((tipo == "filme" && cursor == 1) || isKeepWatchingContainerOpen) return;

  if (onModel)
    setModelCursor(modelCursor + 1 <= filmSerie.seasons.length - 1, 1);

  setCursor(cursor + 1 <= 4, 1);
  if (cursor == 2) {
    setCursor(cursor + 1 <= 4, 1);
    navegationOnPage();
    settingSelected(-1);
  } else {
    navegationOnPage();
    settingSelected(-1);
  }
}

function enterAction() {
  if (onModal) {
    goTo("../profile-select/index.html");
  } else {
    if (isKeepWatchingContainerOpen == true && onModal == false) {
      const keepMovie = dataListAll.keep_watching.find((element) => {
        return element.id == idFilmSerie;
      });
      switch (horizontalCursor) {
        case 0:
          if (tipo == "filme") {
            settingIdsWatchFilm(keepMovie.position);
          } else {
            settingIdsWatchSerie(positionKeep);
          }
          goTo("../watch/index.html");

          break;
        case 1:
          if (tipo == "filme") {
            settingIdsWatchFilm(0);
          } else {
            settingIdsWatchSerie();
          }
          goTo("../watch/index.html");
          break;
        default:
          break;
      }
    } else {
      if (cursor == 0) {
        window.history.back();
      } else {
        if (onModel) enterOnModel();
        else enterOutModel();
      }
    }
  }
}

function enterAssitirButton() {
  tipo === "filme" ? settingIdsWatchFilm() : settingIdsWatchLastSerie();
  goTo("../watch/index.html");
}

function enterAssitirEpButton(epIndex) {
  setLocalStorage("id_entry", filmSerieEpisodes[epIndex].id_kaltura);
  setLocalStorage("titulo", filmSerieTemps.title);
  setLocalStorage(
    "duration",
    filmSerie.seasons[tempSelected[0]].videos[epIndex].duration
  );

  setLocalStorage("episodio", "Episódio " + filmSerieEpisodes[epIndex].order);

  setLocalStorage("position", "0");
}

function enterListButton() {
  changeFavorite(
    "https://watch.tv.br/v2/favorite?id_perfil=" +
    id_perfil +
    "&item_id=" +
    idFilmSerie +
    "&item_type=" +
    type_item +
    "&token=" +
    token
  );
  replaceMyList();
}

function enterTempButton() {
  setOnModel(true);
  openPopUp();
  document
    .getElementsByClassName("details--temps-popup-buttons")
  [modelCursor].classList.add("selected");
}

function enterOutModel() {
  switch (cursor) {
    case 0:
      history.back();
      break;
    case 1:
      if (shouldShowKeepWatching) {
        keepWatchingContainer.style.display = "flex";
        containerElement.style.display = "none";
        isKeepWatchingContainerOpen = true;
        break;
      }

      tipo === "filme" ? settingIdsWatchFilm() : settingIdsWatchLastSerie();
      goTo("../watch/index.html");
      break;
    case 2:
      changeFavorite(
        "https://watch.tv.br/v2/favorite?id_perfil=" +
        id_perfil +
        "&item_id=" +
        idFilmSerie +
        "&item_type=" +
        type_item +
        "&token=" +
        token
      );
      replaceMyList();
      break;
    case 3:
      setOnModel(true);
      openPopUp();
      document
        .getElementsByClassName("details--temps-popup-buttons")
      [modelCursor].classList.add("selected");
      break;
    case 4:
      keepSeries();
    default:
      break;
  }
}

async function keepSeries() {
  tempSelected = tempSelected.split("-");
  dataListAll.keep_watching.forEach((element) => {
    if (
      element.episode ==
      filmSerie.seasons[tempSelected[0]].videos[cursorEpisode].id
    ) {
      keepWatchingContainer.style.display = "flex";
      positionKeep = element.position;
    }
  });

  if (positionKeep == 0) {
    settingIdsWatchSerie();
    goTo("../watch/index.html");
  } else {
    isKeepWatchingContainerOpen = true;
  }
}

function settingIdsWatchFilm(position) {
  setLocalStorage("id_entry", filmSerie.id_kaltura);
  setLocalStorage("titulo", filmSerie.title);
  setLocalStorage("duration", filmSerie.duration);

  setLocalStorage("position", position);
}

function settingIdsWatchSerie(position) {
  setLocalStorage("id_entry", filmSerieEpisodes[cursorEpisode].id_kaltura);
  setLocalStorage("titulo", filmSerieTemps.title);
  setLocalStorage(
    "duration",
    filmSerie.seasons[tempSelected[0]].videos[cursorEpisode].duration
  );

  setLocalStorage(
    "episodio",
    "Episódio " + filmSerieEpisodes[cursorEpisode].order
  );

  if (position) setLocalStorage("position", position);
  else setLocalStorage("position", "0");
}

function settingIdsWatchLastSerie() {
  setLocalStorage(
    "id_entry",
    filmSerieEpisodes[filmSerieEpisodes.length - 1].id_kaltura
  );
  setLocalStorage("titulo", filmSerieTemps.title);
  setLocalStorage(
    "episodio",
    "Episódio " + filmSerieEpisodes[filmSerieEpisodes.length - 1].order
  );
  setLocalStorage(
    "duration",
    filmSerieEpisodes[filmSerieEpisodes.length - 1].duration
  );
  setLocalStorage("position", "0");
}

function enterOnModel() {
  document.getElementById("details-temp-popup").style.display = "";
  document
    .getElementsByClassName("details--temps-popup-buttons")
  [modelCursor]?.classList.remove("selected");
  setTempShowed(modelCursor);

  tempSelected = document.getElementsByClassName(
    "details--temps-popup-buttons"
  )[modelCursor].id;
  filmSerieEpisodes = filmSerie.seasons[modelCursor].videos;
  unbuildingHTMLEpisodes();
  buildingHTMLEpisodes(filmSerieEpisodes);
  modelCursor = 0;
  cursor = 3;
  addClass("details-temp-button", "selected");
  setOnModel(false);
}

function navegationOnPage() {
  switch (cursor) {
    case 0:
      removeClass("details-button-episode", "selected");
      removeClass("details-button-list", "selected");
      addClass("details--button", "selected");
      goingTo("details--button");
      break;
    case 1:
      removeClass("details-temp-button", "selected");
      removeClass("details--button", "selected");
      removeClass("details-button-list", "selected");
      addClass("details-button-episode", "selected");

      break;
    case 2:
      removeClass("details-button-episode", "selected");
      removeClass("details-temp-button", "selected");
      addClass("details-button-list", "selected");

      break;
    case 3:
      if (tipo != "filme") {
        removeClass("details-button-episode", "selected");
        removeClass("details-button-list", "selected");
        addClass("details-temp-button", "selected");
        removeClass("episode-container0", "episode-selected");
        let episode2 = document.getElementsByClassName("episode--container");
        episode2[cursorEpisode].classList.remove("episode-selected");
        onEpisodes = false;
        goingTo("details--button");
      } else {
        cursor = 2;
      }
      break;
    case 4:
      if (tipo != "filme") {
        removeClass("details-temp-button", "selected");
        goingTo("episode-container0");
        let episode = document.getElementsByClassName("episode--container");
        episode[cursorEpisode]?.classList.add("episode-selected");
        onEpisodes = true;
      } else {
        cursor = 2;
      }
      break;
    default:
      break;
  }
}

function addClass(addClass, classAdded) {
  let el = document.getElementById(addClass)?.classList.add(classAdded);

  return el;
}

function removeClass(removeClass, classRemoved) {
  let el = document.getElementById(removeClass)?.classList.remove(classRemoved);

  return el;
}

function goingTo(classGo) {
  let el = document.getElementById(classGo);
  el.scrollIntoView({ behavior: "smooth", block: "center" });
}

function buildingHTMLDetails(element, image) {
  let img = document.getElementById("details-img");
  img.src = image;

  let text = document.getElementById("details-title");
  text.innerText = element.title;

  if (tipo !== "serie") {
    let censorship = document.getElementById("censorship");
    censorship.innerText = element.censorship;
  }
  let button = document.getElementById("details-button-episode");
  if (tipo === "serie") button.innerHTML = "Episódio mais recente";
  else button.innerHTML = "Assistir";

  let myList = document.getElementById("details-button-list");
  myList.innerHTML = "+ FAVORITOS";

  switch (element.censorship) {
    case "0":
      censorship.style.backgroundColor = "green";
      censorship.innerText = "L";
      break;
    case "10":
      censorship.style.backgroundColor = "blue";
      censorship.innerText = "10";
      break;
    case "12":
      censorship.style.backgroundColor = "yellow";
      censorship.innerText = "12";
      break;
    case "14":
      censorship.style.backgroundColor = "orange";
      censorship.innerText = "14";
      break;
    case "16":
      censorship.style.backgroundColor = "red";
      censorship.innerText = "16";
      break;
    case "18":
      censorship.style.backgroundColor = "black";
      censorship.innerText = "18";
      break;
    case "A0":
      censorship.style.backgroundColor = "green";
      censorship.innerText = "AL";
      break;
    case "A10":
      censorship.style.backgroundColor = "blue";
      censorship.innerText = "A10";
      break;
    case "A12":
      censorship.style.backgroundColor = "yellow";
      censorship.innerText = "A12";
      break;
    case "A14":
      censorship.style.backgroundColor = "orange";
      censorship.innerText = "A14";
      break;
    case "A16":
      censorship.style.backgroundColor = "red";
      censorship.innerText = "A16";
      break;
    case "A18":
      censorship.style.backgroundColor = "black";
      censorship.innerText = "A18";
      break;
    default:
      break;
  }

  let genre = document.getElementById("genre");
  if (tipo === "film")
    genre.innerHTML =
      element.genres +
      ", " +
      element.year +
      ", " +
      calcDuration(element.duration);
  else genre.innerHTML = element.genres + ", " + element.year;

  let synopsis = document.getElementById("synopsis");
  synopsis.innerHTML = element.synopsis;

  let director = document.getElementById("director");
  if (tipo === "film") {
    director.innerHTML = "DIRETOR: " + element.director;
    director.style.display = "block";
  }

  let cast = document.getElementById("type-cast");
  if (tipo !== "serie" && element.actors) {
    cast.innerText = "ELENCO: " + element.actors;
  }
}

function buildingHTMLButtonTemp(element) {
  let buttonTemp = document.getElementById("details-temp-button");

  let arrowDown = document.createElement("img");
  arrowDown.src = "../../images/down-arrow.png";
  arrowDown.style.width = "1.54583vw";
  arrowDown.style.height = "0.9625vw";
  arrowDown.style.paddingLeft = "30px";

  buttonTemp.innerHTML = element.title;
  buttonTemp.appendChild(arrowDown);

  let popupChild = document.createElement("div");
  let popup = document.getElementById("details-temp-popup");
  popup.appendChild(popupChild);

  popupChild.classList.add("scrollview-temps");

  filmSerie.seasons.forEach((element, index) => {
    let el = createTagButton();
    el.onclick = function () {
      document.getElementById("details-temp-popup").style.display = "";
      document
        .getElementsByClassName("details--temps-popup-buttons")
      [index]?.classList.remove("selected");
      setTempShowed(index);

      tempSelected = document.getElementsByClassName(
        "details--temps-popup-buttons"
      )[modelCursor].id;
      filmSerieEpisodes = filmSerie.seasons[index].videos;
      unbuildingHTMLEpisodes();
      buildingHTMLEpisodes(filmSerieEpisodes);
      modelCursor = 0;
      cursor = 3;
      addClass("details-temp-button", "selected");
      setOnModel(false);
    };
    el.classList.add("details--temps-popup-buttons");
    el.innerHTML = element.title;
    el.id = index + "-temporada";
    popupChild.appendChild(el);
  });
}

function buildingHTMLEpisodes(filmSerieEpisodes) {
  let episodeTrack = document.getElementById("temps-track");
  filmSerieEpisodes.forEach(function (element, index) {
    let idEpisode;
    dataListAll.keep_watching.forEach((element2) => {
      if (element.id == element2.episode) idEpisode = element2;
    });

    let episode = createTagDiv();

    episode.onclick = () => {
      enterAssitirEpButton(index);
      goTo("../watch/index.html");
    };

    episode.classList.add("episode--container");
    episode.classList.add("swiper-slide");
    episode.id = "episode-container" + index;

    let episodeImg = createTagDiv();
    episodeImg.classList.add("episode--container2-img");

    let duration = createTagDiv();
    duration.classList.add("duration");

    let imgCont = createTagDiv();
    imgCont.classList.add("episode--container-img2");

    let img = createTagImg();
    img.classList.add("episode--container-img");
    img.src = buildUrlImage(element.cdn, element.highlight);

    imgCont.appendChild(img);

    let textImg = createTagDiv();
    textImg.classList.add("episode--container-text");

    let texth2 = createTagH2();
    texth2.classList.add("episode--container-text-h2");
    texth2.innerHTML = element.order + ". " + element.title;

    let synopsisContainer = createTagDiv();
    synopsisContainer.classList.add("episode--container2-synopsis");

    let synopsisContainer3 = createTagDiv();
    synopsisContainer3.classList.add("episode--container3-synopsis");

    let synopsis = createTagSpan();
    synopsis.classList.add("episodes--container-synopsis");

    synopsis.innerHTML = element.synopsis;

    textImg.appendChild(texth2);

    if (idEpisode) {
      if (idEpisode.episode == element.id) {
        duration.style.width =
          (idEpisode.position * 100) / element.duration + "%";
        synopsisContainer.appendChild(duration);
      }
    }
    synopsisContainer3.appendChild(synopsis);
    synopsisContainer.appendChild(synopsisContainer3);

    episode.appendChild(episodeImg);
    episode.appendChild(synopsisContainer);

    episodeImg.appendChild(img);
    episodeImg.appendChild(textImg);

    episodeTrack.appendChild(episode);
  });
}

function unbuildingHTMLEpisodes() {
  let episodeTrack = document.getElementsByClassName("episode--container");

  for (let i = 0; episodeTrack.length - 1 >= 0; i++) {
    let episodeId = document.getElementById("episode-container" + i);
    episodeId.remove();
  }
}

function openPopUp() {
  let el = document.getElementById("details-temp-popup");
  if (onModel) el.style.display = "flex";
  else el.style.display = "none";
}

function buildUrlImage(cdnId, imageName) {
  let urlCdn = mainListAll.cdnlist.find((cdn) => cdn.id === cdnId);
  return urlCdn?.value
    .replace("{image}", imageName)
    .replace("{ks}", dataListAll.ks);
}

function replaceMyList() {
  listOn = !listOn;
  let myList = document.getElementById("details-button-list");

  if (listOn) myList.innerHTML = "- FAVORITOS";
  else myList.innerHTML = "+ FAVORITOS";
}

function settingSelected(valor) {
  if (onModel) {
    let buttonModel = document.getElementsByClassName(
      "details--temps-popup-buttons"
    );
    buttonModel[modelCursor]?.classList.add("selected");
    buttonModel[modelCursor + valor]?.classList.remove("selected");
    buttonModel[modelCursor].scrollIntoView({
      behavior: "smooth",
      block: "center",
    });
  }
}

function settingSelectedEpisode(valor) {
  if (onEpisodes) {
    let episode = document.getElementsByClassName("episode--container");
    episode[cursorEpisode]?.classList.add("episode-selected");
    episode[cursorEpisode + valor]?.classList.remove("episode-selected");
  }
}

async function changeFavorite(url) {
  try {
    let request = await axios(url, { method: "POST" });

    return request.data;
  } catch {
    goTo("../profile-select/index.html");
  }
}

async function getDataList(url) {
  try {
    let request = await axios(url, { method: "POST" });

    return request.data;
  } catch {
    goTo("../profile-select/index.html");
  }
}

async function getMainList(url) {
  try {
    let request = await axios(url, { method: "POST" });

    return request.data;
  } catch {
    goTo("../profile-select/index.html");
  }
}

async function postDetails(url) {
  try {
    let request = await axios(url, { method: "POST" });

    return request.data;
  } catch {
    goTo("../profile-select/index.html");
  }
}

function swiperSlide(arrowRight) {
  arrowRight ? swiper.slideNext(300) : swiper.slidePrev(300);
}

function setTempShowed(indexTemp) {
  let buttonTemp = document.getElementById("details-temp-button");
  buttonTemp.innerHTML = filmSerie.seasons[indexTemp].title;
  let arrowDown = document.createElement("img");
  arrowDown.src = "../../images/down-arrow.png";
  arrowDown.style.width = "1.54583vw";
  arrowDown.style.height = "0.9625vw";
  arrowDown.style.paddingLeft = "30px";

  buttonTemp.appendChild(arrowDown);

  return buttonTemp;
}

function goTo(path) {
  let place = (window.location.href = path);

  return place;
}

function setCursor(condition, valor) {
  if (condition) cursor += valor;

  return cursor;
}

function setCursorEpisode(condition, valor) {
  if (onEpisodes && condition) cursorEpisode += valor;

  return cursorEpisode;
}

function setModelCursor(condition, valor) {
  if (onModel && condition) modelCursor += valor;

  return modelCursor;
}

function calcDuration(element) {
  var result = element / 60;

  return Math.round(result) + " min";
}

function setLocalStorage(key, thing) {
  let item = localStorage.setItem(key, thing);

  return item;
}

function getFromLocalStorage(key) {
  let item = localStorage.getItem(key);

  return item;
}

